.tours {
  position: relative;
  padding-top: 54px;
  padding-bottom: 89px;

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 547px;
    height: 1240px;
    background: url("../img/tour-clouds-desktop.png") no-repeat center;
    background-size: cover;
    right: 0;
    bottom: -475px;
    z-index: -50;
  }

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 418px;
    height: 279px;
    background: url("../img/clouds-tours.png") no-repeat center;
    background-size: cover;
    left: 0;
    bottom: -60px;
    z-index: -50;
  }

  &__wrap {
    @include wrap-desktop;
  }

  &__item-img-mobile {
    display: none;
  }

  &__title-block {
    display: flex;
    justify-content: space-between;
  }

  &__decoration-title {
    @include decoration-title-desktop;
    margin: 0;
  }

  &__title {
    @include title-desktop;
    margin: -48px 0 0 0;
  }

  &__buttons-container {
    display: flex;
    justify-content: space-between;
    width: 79px;
    margin: 34px 2px 43px 0;
  }

  &__button-previous {
    @include button-slider;
  }

  &__button-next {
    @include button-slider;
    transform: rotate(180deg);
  }

  &__list {
    @include list-default;
    display: flex;
    transition: transform 0.8s;
  }

  &__item {
    position: relative;
    width: 385px;
    min-height: 412px;
    padding: 206px 21px 20px;
    margin-right: 21px;
    margin-bottom: 20px;
  }

  &__item:nth-child(3n) {
    margin-right: 0;
  }

  &__item-img {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
    width: 100%;
    height: 100%;
  }

  &__description-item {
    display: flex;
    align-items: center;
  }

  &__location {
    display: block;
    width: 114.5px;
    color: $white-color;
    font-family: $montserrat;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    background-color: $blue-color;
    text-align: center;
    position: relative;
    padding: 7px 0 6px 12px;
    margin-right: 32px;

    &::before {
      content: "";
      position: absolute;
      width: 9px;
      height: 12px;
      background: url("../img/geolocation.svg") no-repeat center;
      top: 9px;
      left: 22px;
    }
  }

  &__item-altai .tours__location {
    &::before {
      left: 26px;
    }
  }

  &__item-adugeya .tours__location {
    &::before {
      left: 20px;
    }
  }

  &__time-days {
    color: $white-color;
    font-family: $montserrat;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    position: relative;
    padding-left: 25px;
    margin-right: 35px;

    &::before {
      content: "";
      position: absolute;
      width: 22px;
      height: 22px;
      background: url("../img/day.svg") no-repeat center;
      top: -3px;
      left: -3px;
    }
  }

  &__item-altai .tours__time-days,
  &__item-adugeya .tours__time-days {
    margin-right: 25px;

    &::before {
      left: -5px;
    }
  }

  &__time-nights {
    color: $white-color;
    font-family: $montserrat;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    position: relative;
    padding-left: 25px;

    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      background: url("../img/moon.svg") no-repeat center;
      top: -3px;
      left: 0;
    }
  }

  &__title-item {
    color: $white-color;
    font-family: $montserrat;
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    width: 242px;
    margin: 23px 0 34px;
  }

  &__item-adugeya .tours__title-item {
    margin: 21px 0 36px -2px;
  }

  &__parameters-title {
    color: $white-color;
    font-family: $montserrat;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    display: block;
    margin-bottom: 2px;
  }

  &__date,
  &__counts {
    color: $white-color;
    font-family: $montserrat;
    font-weight: 700;
    font-size: 13px;
    line-height: 21px;
  }

  &__rating {
    display: flex;
    margin-top: 8px;
  }

  &__star {
    display: block;
    width: 15px;
    height: 14px;
    margin-right: 0.7px;
    background: url("../img/star-empty.svg") no-repeat center;
  }

  &__star--active {
    background: url("../img/star-full.svg") no-repeat center;
  }

  &__parameters {
    display: flex;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      display: block;
      width: 79px;
      height: 1px;
      background: $white-color;
      opacity: 0.28;
      top: -18px;
    }
  }

  &__hard-block {
    margin-right: 44px;
  }

  &__date-block {
    margin-right: 50px;
  }

  &__all-tours-link {
    color: #2d383f;
    font-family: $montserrat;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    display: block;
    margin: 40px auto 0;
    width: 220px;
    position: relative;
    padding-right: 20px;

    &::before {
      position: absolute;
      content: "";
      display: block;
      width: 9px;
      height: 9px;
      transform: rotate(45deg);
      border-right: 2px solid $granit-color;
      border-top: 2px solid $granit-color;
      right: 2px;
      top: 5px;
    }

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  &__list--no-js {
    flex-wrap: wrap;
  }
}

