    }

    &__time-nights {
      font-size: 12px;
      line-height: 15px;
      padding-left: 25px;

      &::before {
        width: 20px;
        height: 20px;
        top: -3px;
      }
    }

    &__title-item {
      font-size: 24px;
      line-height: 29px;
      width: 100%;
      margin: 23px 0 34px;
    }

    &__item-adugeya .tours__title-item {
      margin: 23px 0 34px;
    }

    &__parameters-title {
      font-size: 12px;
      line-height: 15px;
      margin-bottom: 2px;
    }

    &__parametres {
      &::before {
        width: 79px;
        top: -18px;
      }
    }

    &__date,
    &__counts {
      font-size: 13px;
      line-height: 21px;
    }

    &__rating {
      margin-top: 8px;
    }

    &__star {
      width: 15px;
      height: 14px;
      margin-right: 0.7px;
    }

    &__hard-block {
      margin-right: 19px;
    }

    &__date-block {
      margin-right: 18px;
    }

    &__all-tours-link {
      font-size: 16px;
      line-height: 20px;
      margin: 8px auto;
      width: 220px;
      padding-right: 20px;

      &::before {
        width: 9px;
        height: 9px;
        border-right: 2px solid $granit-color;
        border-top: 2px solid $granit-color;
        right: 2px;
        top: 5px;
      }
    }
  }
}
