.training {
  border-bottom: 1px solid #d9e7ea;
  margin-bottom: 114px;

  &__wrap {
    @include wrap-desktop;
    padding-left: 123px;
  }

  &__decoration-title {
    @include decoration-title-desktop;
    margin: 0;
  }

  &__title {
    @include title-desktop;
    margin: -49px 0 25px -2px;
  }

  &__description {
    font-family: $montserrat;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    width: 68.8%;
    margin: 0 0 57px;
  }

  &__title-slider-block {
    display: flex;
    justify-content: space-between;
  }

  &__title-slider,
  &__title-process {
    font-family: $montserrat;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    margin: 0;
    color: $granit-color;
  }

  &__title-slider {
    margin-bottom: 34px;
  }

  &__title-process {
    margin: 52px 0 36px;
  }

  &__buttons-container {
    display: flex;
    justify-content: space-between;
    width: 79px;
    margin: -6px 2px 0 0;
  }

  &__button-previous {
    @include button-slider;
  }

  &__button-next {
    @include button-slider;
    transform: rotate(180deg);
  }

  &__slider-list {
    @include list-default;
    display: flex;
    transition: transform 0.8s;
  }

  &__slider-item {
    width: 234px;
    margin-right: 5px;
    margin-bottom: 5px;
    padding: 14px 15px 16px 17px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.09);
  }

  &__slider-item:nth-child(5n) {
    margin-right: 0;
  }

  &__item-title-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__item-title {
    font-family: $montserrat;
    font-weight: 700;
    font-size: 14px;
    line-height: 19px;
    width: 120px;
    margin: 17px 0 0;
  }

  &__item-description {
    font-family: $montserrat;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    margin: 9px 0 0;
  }

  &__social-link {
    display: block;
    width: 24.66px;
    height: 24.05px;
    border: 1px solid #9ac2e6;
    text-align: center;
    margin: 10px 0 0;

    &:hover,
    &:focus {
      opacity: 0.7;
    }
  }

  &__decoration-tablet {
    display: none;
  }

  &__process-wrap {
    position: relative;

    @include wrap-desktop;
    padding: 0 101px 21px 123px;
  }

  &__process-list {
    @include list-default;
    display: flex;
  }

  &__process-item {
    font-family: $montserrat;
    font-weight: 400;
    font-size: 16px;
    line-height: 23px;
    color: #2d383f;
    width: 265px;
    position: relative;
    padding-left: 37px;
  }

  &__process-item-learning {
    margin-right: 54px;

    &::before {
      content: "";
      position: absolute;
      width: 25px;
      height: 25px;
      background: url("../img/map.svg") no-repeat center;
      background-size: contain;
      left: 0;
    }
  }

  &__process-item-preparing {
    margin-right: 59px;

    &::before {
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      background: url("../img/boots.svg") no-repeat center;
      background-size: contain;
      left: 0;
    }
  }

  &__process-item-lecture {
    width: 217px;
    margin-right: 117px;

    &::before {
      content: "";
      position: absolute;
      width: 23px;
      height: 23px;
      background: url("../img/pine-tree.svg") no-repeat center;
      background-size: contain;
      left: 0;
    }
  }

  &__process-item-interpretation {
    width: 235px;

    &::before {
      content: "";
      position: absolute;
      width: 25px;
      height: 25px;
      background: url("../img/mountain.svg") no-repeat center;
      background-size: contain;
      left: 0;
      top: -2px;
    }
  }

  &__decoration-desktop {
    width: 100%;
    position: absolute;
    bottom: -7.5px;
    left: 0;
  }

  &__br-mobile {
    display: none;
  }

  &__slider-list--no-js {
    flex-wrap: wrap;
  }
}

@media (max-width: $desktop-width) {
