      margin: calc(-6 / #{$vw-desktop} * 100vw) calc(2 / #{$vw-desktop} * 100vw) 0 0;
    }

    &__button-previous,
    &__button-next {
      width: calc(37 / #{$vw-desktop} * 100vw);
      height: calc(35 / #{$vw-desktop} * 100vw);
    }

    &__slider-item {
      width: calc(234 / #{$vw-desktop} * 100vw);
      margin-right: calc(5 / #{$vw-desktop} * 100vw);
      margin-bottom: calc(5 / #{$vw-desktop} * 100vw);
      padding: calc(14 / #{$vw-desktop} * 100vw) calc(15 / #{$vw-desktop} * 100vw) calc(16 / #{$vw-desktop} * 100vw) calc(17 / #{$vw-desktop} * 100vw);
    }

    &__item-title {
      font-size: calc(14 / #{$vw-desktop} * 100vw);
      line-height: calc(19 / #{$vw-desktop} * 100vw);
      width: calc(120 / #{$vw-desktop} * 100vw);
      margin: calc(17 / #{$vw-desktop} * 100vw) 0 0;
    }

    &__item-img {
      width: calc(204 / #{$vw-desktop} * 100vw);
      height: calc(210 / #{$vw-desktop} * 100vw);
    }

    &__item-description {
      font-size: calc(12 / #{$vw-desktop} * 100vw);
      line-height: calc(15 / #{$vw-desktop} * 100vw);
      margin: calc(9 / #{$vw-desktop} * 100vw) 0 0;
