    }

    &__process-wrap {
      padding: 0 calc(101 / #{$vw-desktop} * 100vw) calc(21 / #{$vw-desktop} * 100vw) calc(123 / #{$vw-desktop} * 100vw);
    }

    &__process-item {
      font-size: calc(16 / #{$vw-desktop} * 100vw);
      line-height: calc(23 / #{$vw-desktop} * 100vw);
      width: calc(265 / #{$vw-desktop} * 100vw);
