      }
    }

    &__process-item-interpretation {
      width: calc(235 / #{$vw-desktop} * 100vw);

      &::before {
        width: calc(25 / #{$vw-desktop} * 100vw);
        height: calc(25 / #{$vw-desktop} * 100vw);
