@media (max-width: $mobile-width) {
  .training {
    &__decoration-title {
      font-size: 30px;
      line-height: 37px;
    }

    &__title {
      font-size: 22px;
      line-height: 27px;
      margin: -27px 0 20px;
    }

    &__title-slider-block,
    &__title-wrap {
      margin: 0 auto;
      padding-left: 20px;
      padding-right: 20px;
    }

    &__description {
      font-size: 14px;
      line-height: 22px;
      width: 95%;
      margin: 0 0 57px 0;
    }

    &__br-mobile {
      display: block;
    }

    &__title-slider,
    &__title-process {
      font-size: 16px;
      line-height: 20px;
    }

    &__title-slider {
      margin-bottom: 13px;
    }

    &__title-process {
      margin: 28px 0 46px;
    }

    &__buttons-container {
      width: 79px;
      margin-top: -7px;
    }

    &__button-previous {
      @include button-slider;
    }

    &__button-next {
      @include button-slider;
    }

    &__slider-list {
      padding: 25px 0 25px 20px;
    }

    &__slider-item {
      min-width: 234px;
      margin-right: 6px;
      margin-bottom: 6px;
      padding: 14px 15px 16px 17px;
    }

    &__slider-item:nth-child(5n) {
      margin-right: 6px;
    }

    &__item-img {
      width: 204px;
      height: 210px;
    }

    &__item-description {
      font-size: 12px;
      line-height: 15px;
      margin: 9px 0 0;
    }

    &__item-title {
      font-size: 14px;
      line-height: 19px;
      width: 120px;
      margin: 17px 0 0;
    }

    &__social-link {
      border: 1px solid #9ac2e6;
      margin: 11px 0 0;
    }

    &__process-wrap {
      padding: 0 20px 21px 20px;
    }

    &__process-item {
      font-size: 14px;
      line-height: 20px;
      padding-left: 37px;
    }

    &__process-list {
      flex-direction: column;
      padding-left: 39px;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        display: block;
        width: 1px;
        height: calc(82% + 6 / #{$vw-mobile} * 100vw);
        background-color: #d9e7ea;
        left: 6px;
        top: 2px;
        z-index: -10;
      }
    }

    &__decoration-tablet {
      display: none;
    }

    &__process-item-learning {
      width: 100%;
      margin-right: 0;
      margin-bottom: 25px;

      &::before {
        width: 25px;
        height: 25px;
        top: -8px;
      }

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background-color: #0266c1;
        left: -40px;
        top: 3px;
      }
    }

    &__process-item-preparing {
      width: 100%;
      margin-right: 0;
      margin-top: 0;
      margin-bottom: 28px;

      &::before {
        width: 24px;
        height: 24px;
        top: -1px;
        left: 0;
      }

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background-color: #0266c1;
        left: -40px;
        top: 3px;
      }
    }

    &__process-item-lecture {
      width: 80%;
      margin-right: 0;

      &::before {
        width: 23px;
        height: 23px;
      }

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background-color: #0266c1;
        left: -40px;
        top: 3px;
      }
    }

    &__process-item-interpretation {
      width: 100%;
      margin-right: 0;
      margin-bottom: 25px;
      padding-left: 35px;

      &::before {
        width: 25px;
        height: 25px;
        top: -4px;
        left: -2px;
      }

      &::after {
        content: "";
        position: absolute;
        display: block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        background-color: #0266c1;
        left: -40px;
        top: 3px;
      }
    }
  }
}
