@media (max-width: $mobile-width) {
  .reviews {
    @include wrap-mobile;

    &__title-block {
      margin-bottom: 31px;
      padding: 0;
    }

    &__decoration-title {
      @include decoration-title-mobile;
    }

    &__title {
      @include title-mobile;
      margin-top: -30px;
    }

    &__buttons-container {
      width: 79px;
      margin-top: 1px;
    }

    &__button-previous,
    &__button-next {
      @include button-slider;
    }

    &__list {
      margin-left: 0;
    }

    &__item {
      width: 100%;
      min-height: 535px;
      margin-right: 0;
      margin-bottom: 15px;
      padding: 46px 10px 20px 13px;
    }

    &__item:nth-child(3n) {
      margin-right: 0;
    }

    &__item-oleinik,
    &__item-bazhenova {
      padding: 46px 10px 20px 13px;
    }

    &__item-img {
      margin-right: 9px;
    }

    &__name-block h3 {
      margin: 30px 0 5px;
    }

    &__name-block span,
    &__name-block h3 {
      font-size: 14px;
      line-height: 19px;
    }

    &__item-bazhenova h3 {
      margin: 30px 0 5px;
    }

    &__first-description,
    &__second-description {
      font-size: 14px;
      line-height: 23px;
    }

    &__first-description {
      margin: 18px 0 21px;

      &::before {
        width: 30px;
        height: 23px;
        top: -7px;
      }
    }

    &__item-bazhenova .reviews__first-description {
      margin: 47px 0 21px;
    }

    &__second-description {
      &::after {
        width: 30px;
        height: 23px;
        right: -2px;
        bottom: -13px;
      }
    }
  }
}
